#experience {
	padding: 50px 15px;
	text-align: center;
	/* border-bottom: 1px solid #dcd9d9 */
}

/* .background-alt {
    background: #f2f2f5;
} */

/* #experience h2 {
	color: #374054
} */

#experience-timeline {
	margin: 30px auto 0 auto;
	position: relative;
	max-width: 1000px
}

#experience-timeline:before {
	position: absolute;
	content: '';
	top: 0;
	bottom: 0;
	left: 303px;
	right: auto;
	height: 100%;
	width: 3px;
	background: #3498db;
	z-index: 0
}

#experience-timeline:after {
	position: absolute;
	content: '';
	width: 3px;
	height: 40px;
	background: #3498db;
	background: linear-gradient(to bottom, #3498db, rgba(52, 152, 219, 0));
	top: 100%;
	left: 303px
}

.vtimeline-content {
	margin-left: 350px;
	background: #0000002e;
	/* border: 1px solid #57CBCC; */
	padding: 15px;
	border-radius: 3px;
	text-align: left
}

.vtimeline-content h3 {
	font-size: 1.5em;
	font-weight: 300;
	/* color: #374054; */
	display: inline-block;
	margin: 0
}

.vtimeline-content h4 {
	font-size: 1.2em;
	font-weight: 300;
	color: #b8b8b8;
	margin: 0 0 15px 0
}

.vtimeline-content p {
	color: #74808a;
	font-size: 0.9em;
	margin: 0
}

.vtimeline-content ul li {
	color: whitesmoke;
	/* font-size: 0.9em; */
	margin: 0
}

.vtimeline-point {
	position: relative;
	display: block;
	vertical-align: top;
	margin-bottom: 30px
}

.vtimeline-icon {
	position: relative;
	color: #fff;
	width: 50px;
	height: 50px;
	background: #3498db;
	border-radius: 50%;
	float: left;
	z-index: 99;
	margin-left: 280px
}

.vtimeline-icon i {
	display: block;
	font-size: 2em;
	margin-top: 10px
}

.vtimeline-date {
	width: 260px;
	text-align: right;
	position: absolute;
	left: 0;
	top: 10px;
	font-weight: 300;
	color: whitesmoke;
}

@media only screen and (max-width: 600px) {
    #experience-timeline {
		display: none;
	}
	
	.vtimeline-content-mobile {
		background: #0000002e;
		/* border: 1px solid #e6e6e6; */
		padding: 15px;
		border-radius: 3px;
		text-align: left
	}

	.vtimeline-content-mobile h3 {
		font-weight: 400;
		float: left;
		color: rgb(55, 64, 84);
		margin: 0px;
	}

	.vtimeline-content-mobile h4 {
		color: #74808a;
		clear: both;
    font-weight: 500;
    margin: 0px 0px 15px;
	}

	.vtimeline-date-mobile {
		width: 260px;
		text-align: right;
		float: right;
		/* left: 0; */
		top: 10px;
		font-weight: 300;
		color: #c0bebe
	}

	.timeline-mobile-text{
		color: #e5e5e5;
	}
  }